.form-div {
    color:white;
    display:flex;
    flex-direction: column;
    align-items: center;
    height:300px;
    justify-content: space-around;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("./background.jpg");
}

.form-div form {
    width: 600px;
    display:flex;
    flex-direction: column;
    text-align:center;
    gap:5px;
}